import { createApp } from 'vue'
import App from './App.vue'
import { createRouter, createWebHashHistory } from 'vue-router'
import { ElLoading } from 'element-plus'
import 'element-plus/dist/index.css'
import 'element-plus/theme-chalk/dark/css-vars.css'
const helloWorld = () => import('@/components/helloWorld.vue');
const varAndConst = () => import('@/components/varAndConst.vue');
const beforeEverything = () => import('./components/beforeEverything.vue');
const apiAndStructure = () => import('./components/apiAndStructure.vue');
// const strictMode = () => import('./components/strictMode.vue')
const dataType = () => import("./components/dataType.vue")
import { useStorage } from '@vueuse/core'
const scroll = useStorage("scroll", 0);

var routes = [{ path: "/", component: beforeEverything, title: "写在前面" },
{ path: "/helloWorld", component: helloWorld, title: "hello World!" },
{ path: '/apiAndStructure', component: apiAndStructure, title: "代码结构与api" },
{ path: '/varAndConst', component: varAndConst, title: "变量与常量" },
// { path: '/strictMode', component: strictMode, title: "严格模式" },
{ path: "/dataType", component: dataType, title: "数据类型" }]
window.routes = routes
const router = createRouter({
    history: createWebHashHistory(),
    routes,
})

window.jumpNum = 0;

if (window.name == "1") {
    console.log("jump")
    window.jumpNum = -1
} 

if (window.name == "") {
    window.name = "1"
} 

window.resizeCallback = [function () {
    document.documentElement.style.setProperty(
        "--page-height",
        `${innerHeight}px`
    );
    document.documentElement.style.setProperty("--page-width", `${document.documentElement.clientWidth - 1}px`);
}]
window.setEditorCallback = []
window.setEditor = () => {
    window.setEditorCallback.forEach(callback => {
        callback()
    });
}
onresize = () => {
    window.resizeCallback.forEach(callback => {
        callback()
    });
}
onresize()

createApp(App).use(router).mount('#app')
const last_url = useStorage("last-url");
setTimeout(() => {
    last_url.value && router.replace(last_url.value)
}, 500);
window.router = router
router.beforeEach((to, before) => {
    if (!(before.fullPath == "/" && before.fullPath == to.path)) {
        last_url.value = to.path
        window.activePage && (window.activePage.value = to.path)
        window.setEditorCallback = []
        window.jumpNum++ > 0 ? (scroll.value = window.mainScroll.value = 0) : (window.setEditorCallback.push(() => {
            console.log('aaa', scroll.value); setTimeout(() => {
                window.mainScroll.value = scroll.value
            }, 400);
        }))
        window.pageLoading = ElLoading.service({
            lock: true,
            text: '加载中，请稍等。'
        })
    }
})


document.documentElement.className = "dark theme"